*{
    margin: 0;
    padding: 0;
}
.card{
    /* width: 80%; */
    height: 100%;
    
    border: 1px solid black;
    padding-top: 30px;
    padding-bottom: 20px;
    margin-top: 20px;
    box-shadow: 1px 1px 5px 2px #888888;
    margin-left: 20px;
}
.card1{
    width: 98%;
    height: 100%;
    
    border: 1px solid rgb(75, 64, 64);
    padding-top: 30px;
    padding-bottom: 20px;
    margin-top: 20px;
    box-shadow: 1px 1px 5px 2px #888888;
}
#heading{
    text-align: center;
    text-decoration: black;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
.column1{
    width: 100%;
    color: black;
}
.container{
    margin-left: 0;
    margin-right: 0;
}
.alertbox{
    background-color: white;
    width: 200px;
    height: 100px;
    z-index: 1;
    margin-left: auto;
    margin-right: auto;

}
.footer{
    position:absolute;
    left:0;
    bottom:0;
    right:0;
}